/* Change all text and icon colors in the player. */
.vjs-matrix.video-js {
  color: rgba(54, 122, 248, 0.831);
}

/* Change the border of the big play button. */
.vjs-matrix .vjs-big-play-button {
  border-color: #2B333F;
}
.video-js .vjs-menu-button .vjs-menu-content {
  /* IE8 - has no alpha support */
  background-color: rgb(58, 196, 255);
  /* Opacity: 1.0 = 100%, 0.0 = 0% */
  background-color: rgba(126, 126, 126, 0.133);
}
.video-js .vjs-slider {
  background: rgba(255, 255, 255, 0);
}
.video-js .vjs-control-bar  {
  /* IE8 - has no alpha support */
  background-color: rgb(58, 196, 255);
  /* Opacity: 1.0 = 100%, 0.0 = 0% */
  background-color: rgba(126, 126, 126, 0);
}
.video-js .vjs-load-progress div {
  
  background: rgba(255, 255, 255, 0);
}
.video-js .vjs-load-progress {
  background: rgba(115, 133, 159, 0.114);
}
/* Change the color of various "bars". */
.vjs-matrix .vjs-volume-level,
.vjs-matrix .vjs-play-progress{
  background-color: rgba(53, 127, 255, 0.453);
}
.vjs-matrix .vjs-slider-bar {
  background: rgba(11, 153, 255, 0);
}

